body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-size: 16px;
  font-family: "Open Sans", sans-serif;
  overflow-x: hidden;
  z-index: 1;
}

.App {
    /* text-align: center; */
    margin: auto;
    width: 85%;
}

.App-header {
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: purple;
  top: 4em;
}

.App-header h1 {
  color: black;
}
@media screen and (max-width: 800px) {
  body {
    font-size: 6px;
    font-family: "Open Sans", sans-serif;
    overflow-x: hidden;
    z-index: 1;
  }
  .App {
    text-align: center;
  }

  .App-header {
    position: relative;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: purple;
    top: 4em;
    font-size: 5em;
    padding: 1em;
    margin-right: 0em;
  }

  .App-header h1 {
    color: black;
    display: none;
  }
}

.header-title button {
  background: black;
  border: 0px;
  padding: 1em 3em;
  transition: all 0.2s ease-out;
  -webkit-transition: all 0.2s ease-out;
  -moz-transition: all 0.2s ease-out;
  -ms-transition: all 0.2s ease-out;
  -o-transition: all 0.2s ease-out;
  border: 2px solid black;
  border-radius: 25px;
}

.header-title button:hover {
  opacity: 0.8;
}

.header-title button a {
  font-family: "Open Sans", sans-serif;
  color: white;
  text-decoration: none;
}

.header-title {
  position: relative;
  font-family: "Montserrat", sans-serif;
  font-size: 3em;
  text-align: left;
  top: 0.2em;
  margin-left: 2em;
  color: purple;
}

.heading-background {
  position: fixed;
  opacity: 0.05;
  font-size: 25em;
  left: 0.2em;
  top: -0.5em;
  z-index: -100;
  color: #9352ae;
}
header {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
header h1 {
  font-size: 1em;
  font-weight: 400;
  padding: 1.5em;
  margin-right: 60em;
  background-color: purple;
  color: white;
  border-radius: 50px 20px;
  font: italic small-caps bold 12px/30px Georgia, serif;
}
@media screen and (max-width: 800px) {
  .header-title {
    position: relative;
    width: 400px;
    font-size: 4em;
    text-align: left;
    top: 2em;
    margin-bottom: 20px;
  }
  header h1 {
    position: relative;
    top: 40px;
    left: 40%;
    width: 30%;
    font-size: 2em;
    padding: 0;
    margin-right: 0;
    margin-top: 2em;
    color: white;
    border-radius: 50px 20px;
    font: italic small-caps bold 12px/30px Georgia, serif;
  }
  .home p button {
    margin: 5px 0;
    padding: 4px 6px;
    font-size: 12px;
    border-radius: 8px;
  }
}

@media screen and (min-width: 800px) and (max-width: 1100px) {
  .header-title {
    position: relative;
    width: 60%px;
    font-size: 3em;
    text-align: left;
    right: 1em;
    margin-bottom: 20px;
  }
  header h1 {
    position: relative;
    top: 40px;
    left: 40%;
    width: 30%;
    font-size: 30px;
    padding: 0;
    margin-right: 0;
    margin-top: 2em;
    color: white;
    border-radius: 50px 20px;
    font: italic small-caps bold 12px/30px Georgia, serif;
  }
  .home p button {
    margin: 0;
    padding: 4px 6px;
    font-size: 15px;
    border-radius: 8px;
  }
}

.about {
  display: flex;
  flex-direction: row;
}

.about-content {
  position: relative;
  left: 15em;
}

.about-content h1 {
  font-family: "Montserrat", sans-serif;
  font-size: 5em;
  text-align: left;
}

.about-content p {
  font-size: 1.3em;
  text-align: justify;
  width: 30em;
}

.about img {
  margin-left: 5em;
  border-radius: 8px;
  width: 300px;
  height: 300px;
  background-color: grey;
}

@media screen and (max-width: 800px) {
	.about {
		position: relative;
		display: flex;
		top: 8em;
	}
	.about img {
		display: none;
	}
	.about-content {
		left: 4em;
	}
	.about-content p {
		font-size: 2.5em;
		text-align: left;
		width: 90%; 
		line-height: 1.6em;
		margin: 0;
	}
	.info {
			font-size: 2.5em;
			text-align: justify;
			width: 90%; 
			line-height: 1.6em;
			margin: 0;
	}
}

@media screen and (min-width: 800px) and (max-width: 1100px){
	.about {
		position: relative;
		display: flex;
		top: 2em;
		right: 1em;
	}
	.about img {
		display: none;
	}
	.about-content {
		left: 4em;
	}
	.about-content p {
		font-size: 1.5em;
		text-align: left;
		width: 80%; 
		line-height: 1.6em;
		margin: 0;
	}
	.info {
		font-size: 1.5em;
		text-align: justify;
		width: 80%; 
		line-height: 1.6em;
		margin: 0;
	}
}
.project img {
  width: 25em;
  height: 30em;
  height: 25em;
  border: 1px solid grey;
}

@media screen and (max-width: 800px) {
  .project img {
    width: 45em;
    height: 45em;
    object-fit: cover;
    border: 1px solid #ddd;
    border-radius: 4px;
    padding: 5px;
    width: 150px;
  }
}

@media screen and (min-width: 800px) and (max-width: 1100px) {
  .project img {
    width: 20em;
    height: 20em;
    object-fit: cover;
  }
}

.work {
  position: relative;
  display: flex;
  flex-direction: column;
  left: 15em;
  top: 8em;
  align-items: flex-start;
}

.work .heading {
  font-family: "Montserrat", sans-serif;
  font-size: 5em;
  text-align: left;
}

.work .work-content {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2em;
}

@media screen and (max-width: 800px) {
  .work {
    left: 4em;
    margin-top: 2em;
    align-items: flex-start;
  }
  .work p {
    left: 4em;
    margin-top: 2em;
    align-items: flex-start;
    size: 1em;
  }
  .work .work-content {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 7em;
    font-size: small;
  }
}

@media screen and (min-width: 800px) and (max-width: 1100px) {
  .work {
    left: 3em;
    margin-top: -4em;
    align-items: flex-start;
  }
  .work p {
    left: 4em;
    margin-top: 2em;
    align-items: flex-start;
    size: 1em;
  }
  .work .work-content {
    display: grid;
    grid-template-columns: repeat(2, 0.5fr);
    grid-gap: 7em;
    font-size: small;
  }
}

.contact {
  position: relative;
  top: 20em;
  left: 15em;
  text-align: left;
}

.contact-content ul div {
  display: inline;
  padding-right: 2em;
}

.contact h1 {
  font-family: "Montserrat", sans-serif;
  font-size: 5em;
  text-align: left;
}

.contact a {
  text-decoration: none;
  color: black;
}

.contact img {
  max-width: 40px;
  max-height: 50px;
}

.amazing {
  color: #9352ae;
  opacity: 1;
}

@media screen and (max-width: 800px) {
  .contact {
    top: 13em;
    left: 4em;
  }
  .contact h1 {
    font-size: 5em;
  }
  .contact-content ul div {
    display: inline;
    padding-right: 2em;
  }
  .contact-content ul {
    padding: 0;
  }
  .contact img {
    height: 30px;
    width: 30px;
  }
}

@media screen and (min-width: 800px) and (max-width: 1100px) {
  .contact {
    top: 10em;
    left: 3em;
  }
  .contact h1 {
    font-size: 3em;
  }
  .contact-content ul div {
    display: inline;
    padding-right: 2em;
  }
  .contact-content ul {
    padding: 0;
  }
  .contact img {
    height: 40px;
    width: 40px;
  }
}

nav {
  position: fixed;
  left: 50em;
  z-index: 100;
  background-color: whitesmoke;
  border-radius: 50px 50px;
  margin-top: 6px;
}

nav a {
  display: inline-block;
  cursor: pointer;
}
nav a.link {
  text-decoration: none;
  color: black;
  padding: 1em;
  opacity: 0.5;
  transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
}
a.link:hover {
  opacity: 1;
}
.active {
  color: black;
  opacity: 1;
}

@media screen and (max-width : 800px){
  nav{
	display:flex;
	justify-content: space-around;
	position:fixed;
	top:0;
	left:0;
	width: 90%;
  	font-size: 14px;
  }
}

@media screen and (min-width: 800px) and (max-width : 1100px){
  nav{
	display:flex;
	justify-content: space-around;
	position:fixed;
	top:0;
	left:0;
	width: 50%;
  	font-size: 14px;
  }
}
.footer {
  position: relative;
  top: 20em;
  padding: 1em;
}

.footer a {
  text-decoration: none;
  color: black;
}

@media screen and (max-width: 800px) {
  .footer {
    top: 7em;
    padding-top: 2em;
    font-size: 2em;
  }
}

@media screen and (min-width: 800px) and (max-width: 1100px) {
  .footer {
    top: 10em;
    padding-top: 2em;
    font-size: 1em;
  }
}

